import back from '@/components/back'
import noData from '@/components/noData'
export default {
    components:{
        back,
		noData
    },
    data(){
        return {
			page: 1,
			list:[],
			haveMore:false,
			pageShow:false,
			active:1,
            items: [],
            act: 0,
            children: [],
            search: '',
            user: this.$store.state.userInfo,
            ListDetails: this.$store.state.upList,
            medSearchList: null,
            inputFlag: false,
			medSearchList2:null,
			medBlurSH:false,
			Refresh:this.$route.query.Refresh
        }
    },
    created(){
        this.pull()
		this.pullAllPrescription()
    },
	mounted(){
		this.getMore()
	},
	 beforeDestroy(){
	    window.onscroll = null
	},
	beforeRouteLeave(to,from,next){   //离开
		window.onscroll = null
		if(from.meta.keepAlive) {
			from.meta.scollTopPosition = this.scrollTopDistance
		}
		next()
	},
	beforeRouteEnter(to, from, next) {  //进入前 
			let flag = false
			next(vm => {
				vm.getMore()
				vm.Refresh = vm.$route.query.Refresh
				if((from.path.indexOf("allPrescription") != -1 && vm.$store.state.prevUrl != null) || !vm.$store.state.isFresh || vm.Refresh){
					flag = true
				}
				if(flag){
					vm.page = 1
					vm.haveMore = true
					to.meta.scollTopPosition = 0
					vm.pageShow = false
					vm.listShow = false
					vm.pullAllPrescription(false)
					vm.$store.dispatch('pushIsFresh',true)
				}
				else{
					setTimeout(()=>{
					    vm.$refs.el.scrollTop = to.meta.scollTopPosition
					},1)
				}
			});
		},
    methods:{
		NewSwtich(){
			this.$store.dispatch('pushsaveInfo', null)
			this.$store.dispatch('pushchList', [])
			this.$store.dispatch('pushchState', 1)
			this.$router.push('/chooseDrugs?Swtich=1')
		},   
		usePre(i){
			this.$store.dispatch('pushchList', [])
			this.$store.dispatch('pushisUsePre',false)
			this.$router.push('/cM/prescription?DataSource=3&DataId='+i.Id+'&subId='+i.MedicationForm) 
		},
		pullAllPrescription(h){
			 this.$post('Prescription/Prescription/GetAllTCMPrescriptionOftenUsed',{
				Name:this.search,
				DoctorId:this.user.Data.User.DoctorId,
				OrgId:this.user.Data.User.OrgId,
				PageIndex:this.page,
				PageSize:15,
			}).then(o => {
				this.pageShow = true
				if (!o.ReturnData || o.ReturnData.length < 15){
					this.haveMore = false
				}
				else {
					this.haveMore = true
				}
				if (h){
				    o.ReturnData.forEach(v => {
				        this.list.push(v)
				    })
				}
				else {
				    this.list = o.ReturnData
				}
				this.list.forEach((a,k) => {
					let numTotal = 0
					a.ListDetails.forEach(v => {
					    numTotal += Number(v.Quantity)
					})
					this.list[k].numTotal = numTotal
				})
			})
		},

        choose(id,idx){
            this.act = idx 
            this.pull(id)
        },
		searchFor(){
			this.$router.push(`/jdPrescription?searchName=`+this.search)
		},
        pull(ParentId){
            if(!ParentId){
                ParentId = 0
                this.$post('Prescription/Prescription/GetPrescriptionClass',{
                    ParentId,
                }).then(o => {
                    this.items = o
                    this.pull(o[0].Id)
                })
            }
            else{
                this.$post('Prescription/Prescription/GetPrescriptionClass',{
                    ParentId,
                }).then(o => {
                    this.children = o
                })
            }
        },
        goList(i){
            this.$router.push(`/jdPrescription?ClassId=${i.Id}`)
        },
		 getMore(){
		    const el = this.$refs.el
		    let timer = null
		    window.onscroll = () => {
		        clearTimeout(timer)
		        timer = setTimeout(() => {
		            const top = document.documentElement.scrollTop
		                        || window.pageYOffset
		                        || document.body.scrollTop,
		                    h = el.scrollHeight,
		                    wh = window.innerHeight
		            if (h - top - wh < 100 && this.haveMore){
		                this.page += 1
		                this.pullAllPrescription(true)
		            }
		        }, 100)
		    }
		},
    }
}



